<template>
    <div class="f5">
        <van-nav-bar title="我的维修记录"/>
            <van-list
                v-model="loading"
                :finished="finished"
                :immediate-check="false"
                finished-text="没有更多了"
                @load="onLoad"
                :offset="10"
            >
            <van-cell-group v-for="(item,index) in repairlist" :key="index">
                <van-cell title="房间/区域" :value="item.area" />
                <van-cell title="类别" :value="item.type+'报修'" />                
                <van-cell title="报修类型" :value="item.frepair_type" />
                <van-cell title="状态" :value="item.frepair_state" />
                <van-cell title="报修时间" :value="item.repair_date" />
                <van-cell title="处理时间" :value="item.frepair_deal_date" />
            </van-cell-group>
        </van-list>
    </div>
</template>

<script>
import {apialreadyrepairlist} from "@/api/index"
export default {
    name:'repairRecord',
    data() {
        return {
            loading: false,
            finished: false,
            page: 1, //请求第几页
            pageSize: 5, //每页请求的数量
            total: 0, //总共的数据条数
            repairlist:[]
        }
    },
    methods: {

        // 上拉加载
        onLoad() {
            this.page++;
            this.getlist();
        },
        getlist(){
            let params = {
                page: this.page,
                pageSize: this.pageSize
            };
            apialreadyrepairlist(params).then(res=>{
                this.loading = false;
                this.total = res.data.count; 
                let rows = res.data.data;
                if (rows == null || rows.length === 0) {
                // 加载结束
                this.finished = true;
                    return;
                }
                this.repairlist = this.repairlist.concat(rows);
                this.isLoading = false;
                if (this.repairlist.length >= this.total) {
                    this.finished = true;
                }
            })
        },
    },
    created(){
        this.getlist()
    }
}
</script>

<style>

</style>


